import React, { FC, useRef } from "react";
import { useHistory } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import { IOrganizationProps } from "../../../context/Organization/types";
import { SVGWrapper } from "./styled";
import { stateColors } from "./colors";
import { getRandomColor } from "../../../utils/helpers";

type IMapProps = {
  statesData: {
    id: string;
    name: string;
    funding: number;
    organizations: IOrganizationProps[];
    numOfOrganizations: number;
  }[];
};

const ranges = {
  lowest: [0, 20],
  low: [21, 50],
  average: [51, 120],
  high: [121, 250],
  highest: [251, "*"],
};

export function getClassName(numOfOrganizations?: number) {
  let className = "";
  for (const key in ranges) {
    let [min, max] = ranges[key];

    if (typeof max === "string") {
      if (numOfOrganizations >= min) {
        return (className = key);
      }
    }

    if (numOfOrganizations >= min && numOfOrganizations <= max) {
      return (className = key);
    }
  }

  return className;
}

